import addDays from "date-fns/addDays";
import format from "date-fns/format";
import React, { useState } from "react";
import DatePicker from "react-datepicker";
import { Link, useLocation } from "react-router-dom";
import { Page } from "common/components";

const DoctorProfile = () => {
  const title = "Doctor Profile";
  let location = useLocation();

  console.log(`DoctorProfile :: location: ${JSON.stringify(location)}`);

  // const { doctorData } = location?.state;

  const [state, setState] = useState({
    key: 1,
    startDate: new Date(),
    islocation: false,
    isoverview: true,
    ...location?.state,
    // doctorData: location?.state?.doctorData?.doctorData,
  });

  console.log(
    `DoctorProfile :: doctorData: ${JSON.stringify(state?.doctorData)}`
  );

  // const [islocation, setIslocation] = useState(false);
  // const [isoverview, setIsoverview] = useState(true);

  const tabclick = (index) => {
    setState({
      ...state,
      islocation: index === 2 ? true : false,
      isoverview: index === 1 ? true : false,
    });
  };

  return (
    <Page title={title}>
      <div className="content">
        <div className="container">
          <div className="card">
            <div className="card-body product-description">
              <div className="doctor-widget">
                <div className="doc-info-left">
                  <div className="doctor-img1 center-img">
                    <img
                      src={state?.doctorData?.image}
                      className="img-fluid"
                      alt="User"
                    />
                  </div>
                  <div className="doc-info-cont">
                    <h4 className="doc-name">{state?.doctorData?.doc_name}</h4>
                    <p className="doc-speciality">{state?.doctorData?.title}</p>
                    <p className="doc-department">
                      <img
                        src={state?.doctorData?.speciality?.img}
                        className="img-fluid"
                        alt="Speciality"
                      />
                      {state?.doctorData?.speciality?.name}
                    </p>
                    <div className="clinic-details">
                      <p className="doc-location">
                        <i className="fas fa-map-marker-alt"></i>
                        {` ${state?.doctorData?.address} - `}
                        <a
                          href={`https://www.google.com/maps/search/?api=1&query=${state?.doctorData?.lat},${state?.doctorData?.lng}`}
                          target="_blank"
                          rel="noopener noreferrer"
                        >
                          Get Directions
                        </a>
                      </p>
                    </div>
                    {/* <div className="clinic-services">
                      <span>Dental Fillings</span>
                      <span>Teeth Whitneing</span>
                    </div> */}
                  </div>
                </div>
                <div className="doc-info-right">
                  <div className="clinic-booking">
                    <div className="form-group">
                      <label>Pickup an Appointment</label>
                      <div>
                        <DatePicker
                          selected={state?.startDate}
                          onChange={(date) => {
                            console.log(`date: ${format(date, "MM/dd/yyyy")}`);
                            setState({ ...state, startDate: date });
                          }}
                          minDate={new Date()}
                          maxDate={addDays(new Date(), 180)}
                          dateFormat="MMMM dd, yyyy"
                          inline
                          // customInput={<ExampleCustomInput />}
                          // withPortal
                        />
                      </div>
                      <button
                        type="button"
                        className="btn w-100 btn-outline-secondary mb-2"
                      >
                        {format(state?.startDate, "dd/MM/yyyy")}
                      </button>
                      <div className="clinic-booking">
                        <Link
                          to="/patient-details"
                          className="apt-btn"
                          state={{
                            ...state,
                            appointmentDate: state?.startDate.toISOString(),
                          }}
                        >
                          Book Appointment1
                        </Link>
                        <Link
                          to="/patient-details2"
                          className="apt-btn"
                          state={{
                            ...state,
                            appointmentDate: format(
                              state?.startDate,
                              "dd/MM/yyyy"
                            ),
                          }}
                          hidden
                        >
                          Book Appointment2
                        </Link>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>

          {/* Doctor Details Tab */}
          <div className="card">
            <div className="card-body pt-0">
              {/* Tab Menu */}
              <nav className="user-tabs mb-4">
                <ul className="nav nav-tabs nav-tabs-bottom nav-justified">
                  <li className="nav-item" onClick={(e) => tabclick(1)}>
                    <a
                      className={`nav-link ${state.isoverview ? "active" : ""}`}
                      href=""
                      onClick={(e) => {
                        e.preventDefault();
                      }}
                      data-toggle="tab"
                    >
                      Overview
                    </a>
                  </li>
                  <li className="nav-item" onClick={(e) => tabclick(2)}>
                    <a
                      className={`nav-link ${state.islocation ? "active" : ""}`}
                      href=""
                      onClick={(e) => {
                        e.preventDefault();
                      }}
                      data-toggle="tab"
                    >
                      Locations
                    </a>
                  </li>
                </ul>
              </nav>
              {/* /Tab Menu */}
              {/* Tab Content */}
              <div className="tab-content pt-0">
                {/* Overview Content */}
                <div
                  role="tabpanel"
                  id="doc_overview"
                  className={
                    state.isoverview
                      ? "tab-pane fade show active"
                      : "tab-pane fade show"
                  }
                >
                  <div className="row">
                    <div className="col-md-9">
                      {/* About Details */}
                      <div className="widget about-widget">
                        <h4 className="widget-title">About Me</h4>
                        <p>
                          Lorem ipsum dolor sit amet, consectetur adipiscing
                          elit, sed do eiusmod tempor incididunt ut labore et
                          dolore magna aliqua. Ut enim ad minim veniam, quis
                          nostrud exercitation ullamco laboris nisi ut aliquip
                          ex ea commodo consequat. Duis aute irure dolor in
                          reprehenderit in voluptate velit esse cillum dolore eu
                          fugiat nulla pariatur. Excepteur sint occaecat
                          cupidatat non proident, sunt in culpa qui officia
                          deserunt mollit anim id est laborum.
                        </p>
                      </div>
                      {/* /About Details */}
                      {/* Awards Details */}
                      <div className="widget awards-widget">
                        <h4 className="widget-title">Awards</h4>
                        <div className="experience-box">
                          <ul className="experience-list">
                            <li>
                              <div className="experience-user">
                                <div className="before-circle" />
                              </div>
                              <div className="experience-content">
                                <div className="timeline-content">
                                  <p className="exp-year">July 2019</p>
                                  <h4 className="exp-title">
                                    Humanitarian Award
                                  </h4>
                                  <p>
                                    Lorem ipsum dolor sit amet, consectetur
                                    adipiscing elit. Proin a ipsum tellus.
                                    Interdum et malesuada fames ac ante ipsum
                                    primis in faucibus.
                                  </p>
                                </div>
                              </div>
                            </li>
                            <li>
                              <div className="experience-user">
                                <div className="before-circle" />
                              </div>
                              <div className="experience-content">
                                <div className="timeline-content">
                                  <p className="exp-year">March 2011</p>
                                  <h4 className="exp-title">
                                    Certificate for International Volunteer
                                    Service
                                  </h4>
                                  <p>
                                    Lorem ipsum dolor sit amet, consectetur
                                    adipiscing elit. Proin a ipsum tellus.
                                    Interdum et malesuada fames ac ante ipsum
                                    primis in faucibus.
                                  </p>
                                </div>
                              </div>
                            </li>
                            <li>
                              <div className="experience-user">
                                <div className="before-circle" />
                              </div>
                              <div className="experience-content">
                                <div className="timeline-content">
                                  <p className="exp-year">May 2008</p>
                                  <h4 className="exp-title">
                                    The Dental Professional of The Year Award
                                  </h4>
                                  <p>
                                    Lorem ipsum dolor sit amet, consectetur
                                    adipiscing elit. Proin a ipsum tellus.
                                    Interdum et malesuada fames ac ante ipsum
                                    primis in faucibus.
                                  </p>
                                </div>
                              </div>
                            </li>
                          </ul>
                        </div>
                      </div>
                      {/* /Awards Details */}
                    </div>
                  </div>
                </div>
                {/* /Overview Content */}
                {/* Locations Content */}
                <div
                  role="tabpanel"
                  id="doc_locations"
                  className={
                    state.islocation
                      ? "tab-pane fade show active"
                      : "tab-pane fade show"
                  }
                >
                  {/* Location List */}
                  <div className="location-list">
                    <div className="row">
                      {/* Clinic Content */}
                      <div className="col-md-6">
                        <div className="clinic-content">
                          <h4 className="clinic-name">
                            <a href="#">Smile Cute Dental Care Center</a>
                          </h4>
                          <p className="doc-speciality">
                            MDS - Periodontology and Oral Implantology, BDS
                          </p>
                          <div className="clinic-details mb-0">
                            <h5 className="clinic-direction">
                              {" "}
                              <i className="fas fa-map-marker-alt"></i> 2286
                              Sundown Lane, Austin, Texas 78749, USA <br />
                              <a
                                href=""
                                onClick={(e) => {
                                  e.preventDefault();
                                }}
                              >
                                Get Directions
                              </a>
                            </h5>
                          </div>
                        </div>
                      </div>
                      {/* /Clinic Content */}
                    </div>
                  </div>
                  {/* /Location List */}
                  {/* Location List */}
                  <div className="location-list">
                    <div className="row">
                      {/* Clinic Content */}
                      <div className="col-md-6">
                        <div className="clinic-content">
                          <h4 className="clinic-name">
                            <a href="#">Smile Cute Dental Care Center</a>
                          </h4>
                          <p className="doc-speciality">
                            MDS - Periodontology and Oral Implantology, BDS
                          </p>
                          <div className="clinic-details mb-0">
                            <h5 className="clinic-direction">
                              {" "}
                              <i className="fas fa-map-marker-alt"></i> 2286
                              Sundown Lane, Austin, Texas 78749, USA <br />
                              <a
                                href=""
                                onClick={(e) => {
                                  e.preventDefault();
                                }}
                              >
                                Get Directions
                              </a>
                            </h5>
                          </div>
                        </div>
                      </div>
                      {/* /Clinic Content */}
                    </div>
                  </div>
                  {/* /Location List */}
                </div>
                {/* /Locations Content */}
              </div>
            </div>
          </div>
          {/* /Doctor Details Tab */}
        </div>
      </div>
    </Page>
  );
}

